body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Clock {
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Display {
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 10px solid blueviolet;
  background: #6495ed;
  color: #fff;
}

.Display__time {
  font-weight: bold;
  font-size: 2rem;
}

.Panel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Panel__label {
  padding: 0.5rem;
  position: relative;
  margin-left: 20px;
  width: 60px;
  height: 20px;
}

.Panel__input {
  display: none;
}

.Panel__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: powderblue;
  border-radius: 34px;
  height: 34px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.Panel__slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  border-radius: 50%;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.Panel__input:checked + .Panel__slider {
  background: cornflowerblue;
}

.Panel__input:checked + .Panel__slider:before {
  -webkit-transform: translateX(40px) scale(1.1);
      -ms-transform: translateX(40px) scale(1.1);
          transform: translateX(40px) scale(1.1);
}



